
import { computed, defineComponent, nextTick, ref } from 'vue'
import PageContent from '@/components/page-content'
import { PageSearch } from '@/components/page-search'
import { contentTableConfig } from './config/content.config'
import { useStore } from '@/store'
import { ElMessage, ElMessageBox, ElTree } from 'element-plus'
import { menuMapLeafKeys } from '@/utils/map-menus'
export default defineComponent({
  name: 'role',
  components: {
    PageContent,
    PageSearch
  },
  setup() {
    const store = useStore()
    const pageName = 'role'
    // 编辑
    const dialogVisible = ref(false)
    const formData = ref({
      id: '',
      name: '',
      code: '',
      remark: ''
    })
    // 编辑
    const handleEditData = (row: any) => {
      dialogVisible.value = true
      formData.value.id = row.id
      formData.value.name = row.name
      formData.value.code = row.code
      formData.value.remark = row.remark
    }
    const editConfirmHandle = () => {
      dialogVisible.value = false
      store.dispatch('system/editPageDataAction', { formData, pageName })
    }
    // 删除
    const handleDeleteData = (row: any) => {
      const id = row.id
      ElMessageBox.confirm('此操作将永久删除该菜单, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const res = await store.dispatch('system/deleteDataAction', { id, pageName })
          if (!res.success) {
            return ElMessage({
              type: 'error',
              message: res.message
            })
          }
          ElMessage({
            type: 'success',
            message: '删除成功!'
          })
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: '已取消删除'
          })
        })
    }
    // 添加
    const dialogVisibleRole = ref(false)
    const addShow = () => {
      dialogVisibleRole.value = true
    }
    const clearForm = () => {
      formData.value.name = ''
      formData.value.code = ''
      formData.value.remark = ''
    }
    const addConfirmHandle = async () => {
      dialogVisibleRole.value = false
      const res = await store.dispatch('system/addAction', { formData, pageName })
      if (!res.success) {
        return ElMessage({
          showClose: true,
          message: '添加失败',
          type: 'error'
        })
      }
      ElMessage({
        showClose: true,
        message: '添加成功',
        type: 'success'
      })
    }
    // 分页
    const pageInfo = ref({
      pageNum: 1,
      pageSize: 6
    })
    const queryInfo = pageInfo.value
    const total = computed(() => store.getters[`system/pageListCount`](pageName))
    const handleSizeChange = (pageSize: any) => {
      pageInfo.value.pageSize = pageSize
      store.dispatch('system/getPageListAction', {
        pageName,
        queryInfo
      })
    }
    const handleCurrentChange = (pageNum: any) => {
      pageInfo.value.pageNum = pageNum
      store.dispatch('system/getPageListAction', {
        pageName,
        queryInfo
      })
    }

    // 分配权限

    const elTreeRef = ref<InstanceType<typeof ElTree>>()
    const dialogVisiblePerm = ref(false)
    const menus = ref<any[]>([])
    const getMenus = async (menus: any) => {
      menus.value = await store.dispatch('system/getPageListAction', { pageName: 'menu' })
    }
    getMenus(menus)
    let roleId = 0
    const handlePermClick = async (row: any) => {
      console.log(row)

      dialogVisiblePerm.value = true
      const roleId = row.id
      const menuList = await store.dispatch('system/getRoleMenuAction', { roleId })
      console.log(menuList)
      const leafKeys = menuMapLeafKeys(menuList)
      nextTick(() => {
        elTreeRef.value?.setCheckedKeys(leafKeys, false)
      })
    }

    const menuIds = ref<any[]>([])
    const handleCheckChange = (data1: any, data2: any) => {
      const checkedKeys = data2.checkedKeys
      const halfCheckedKeys = data2.halfCheckedKeys
      const menuList = [...checkedKeys, ...halfCheckedKeys]
      menuIds.value = menuList
    }

    const assignConfirmHandle = async () => {
      const res = await store.dispatch('system/assignMenuAction', { roleId, menuIds })
      dialogVisiblePerm.value = false
      if (!res.success) {
        return ElMessage({
          showClose: true,
          message: '分配权限失败',
          type: 'error'
        })
      }
      ElMessage({
        showClose: true,
        message: '分配权限成功',
        type: 'success'
      })
    }
    return {
      contentTableConfig,
      handleDeleteData,
      handleEditData,
      formData,
      dialogVisible,
      editConfirmHandle,
      addConfirmHandle,
      addShow,
      dialogVisibleRole,
      clearForm,
      handleSizeChange,
      handleCurrentChange,
      pageInfo,
      total,
      dialogVisiblePerm,
      handlePermClick,
      assignConfirmHandle,
      menus,
      handleCheckChange,
      elTreeRef
    }
  }
})
